import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';

export default function Nav() {

  // AppBar Styles and Collapse Theme
  const useStyles = makeStyles((theme) => ({
    appbar: {
      color: 'black',
      background: 'transparent',
      boxShadow: 'none'
    },
    collapse: {
      [theme.breakpoints.up('md')]: {
          display: 'none',
      },
    },
    noCollapse: {
      display: 'none',
      [theme.breakpoints.up('md')]: {
        display: 'block',
      },
    }
  }));

  // Calling AppBar Styles and Collapse Theme
  const classes = useStyles();
  const theme = useTheme();

  // Dropdown Menu Functions
  const [open, setOpen] = React.useState(false);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  // Misc. Inline Styles
  const logo = {
    color: 'black',
    textDecoration: 'none',
    border: '3px solid black',
    padding:'3px 3px 3px 3px', 
    fontWeight:'bold', 
    fontSize:'14px',
    letterSpacing: '.1em'
  }

  return (
    <div className='nav'>
      <AppBar className={classes.appbar} position='static'>
        <Toolbar>
          <IconButton className={classes.collapse} color='inherit' edge='start' onClick={handleDrawerOpen}>
            <MenuIcon />
          </IconButton>

          <Grid container>
            {/* Categories */}
            <Grid item container xs={3} md={5} className={classes.noCollapse}>
              <ul className='menu'>
                <li className='clothes'>
                  <Link to='/finery/clothes'>CLOTHES</Link>
                  <ul className='dropdown'>
                      <Link to='/finery/clothes/tops'><li className='dropdownLinks'>TOPS</li></Link>
                      <Link to='/finery/clothes/bottoms'><li className='dropdownLinks'>BOTTOMS</li></Link>
                      <Link to='/finery/clothes/outer'><li className='dropdownLinks'>OUTER</li></Link>
                  </ul>
                </li>
                <li className='shoes'>
                  <Link to='/finery/shoes'>SHOES</Link>
                  <ul className='dropdown'>
                      <Link to='/finery/shoes/boots'><li className='dropdownLinks'>BOOTS</li></Link>
                      <Link to='/finery/shoes/heels'><li className='dropdownLinks'>HEELS</li></Link>
                  </ul>
                </li>
                <li className='accessories'>
                  <Link to='/finery/accessories'>ACCESSORIES</Link>
                  <ul className='dropdown'>
                      <Link to='/finery/accessories/bags'><li className='dropdownLinks'>BAGS</li></Link>
                  </ul>
                </li>
              </ul> 
            </Grid>
            {/* Logo */}
            <Grid item container xs={12} sm={12} md={2} justify='center'>
              <h5><Link to='/finery' style={logo}>FINERY</Link></h5>
            </Grid>
            {/* Dashboard */}
            <Grid item container md={5} className={classes.noCollapse}>
            <ul className='menu'>
              <li><Link to='/'>SEARCH</Link></li>
              <li><Link to='/'>SIGN IN/UP</Link></li>
              <li><Link to='/'>CART</Link></li>
            </ul> 
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>

      <Drawer anchor='top' open={open}>
        <div>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          <ListItem>
            <Link to='/finery/clothes' style={{color:'black',textDecoration:'none'}}>clothes</Link>
          </ListItem>
          <ListItem>
            <Link to='/finery/shoes' style={{color:'black',textDecoration:'none'}}>shoes</Link>
          </ListItem>
          <ListItem>
            <Link to='/finery/accessories' style={{color:'black',textDecoration:'none'}}>accessories</Link>
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem>
            <Link to='/finery' style={{color:'black',textDecoration:'none'}}>search</Link>
          </ListItem>
          <ListItem>
            <Link to='/finery' style={{color:'black',textDecoration:'none'}}>log in / sign up</Link>
          </ListItem>
          <ListItem>
            <Link to='/finery' style={{color:'black',textDecoration:'none'}}>cart</Link>
          </ListItem>
        </List>
      </Drawer>
    </div>
  );
}